.works-container {
  max-width: 1000px;
}

.works-wrapper {
  display: flex;
  margin-bottom: 100px;
  justify-content: space-around;
}

.works-wrapper:nth-child(odd) {
  flex-direction: row-reverse;
  justify-content: space-between;
}

.works-wrapper:nth-child(odd) .img-wrapper {
  margin-right: 0;
  margin-left: 3rem;
}

.img-wrapper {
  width: 50%;
  margin-right: 3rem;
  position: relative;
}

.img-wrapper a {
  display: inline-block;
  width: 100%;
  height: auto;
}

.img-wrapper img {
  border-radius: 5px;
}

.img-wrapper::before {
  content: "";
  position: absolute;
  /* width: 100%; */
  border-radius: 5px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #193b4e;
  opacity: 0.7;
  transition: all 1s ease;
  pointer-events: none;
}

.img-wrapper:hover::before {
  opacity: 0;
}

.works-img {
  width: 100%;
  height: auto;
}

.text-wrapper {
  width: 50%;
}

.text-wrapper h3 {
  color: #63f2d0;
  margin-bottom: 1rem;
}

.text-wrapper p {
  color: #abc5e6;
  background-color: #19253b;
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 5px;
}

.tec-list ul {
  display: flex;
  list-style: none;
  color: #abc5e6;
}

.tec-list li {
  margin-right: 1rem;
}

.works-link {
  display: flex;
  justify-content: flex-start;
}

.icon {
  padding: 1rem 1rem 0.5rem 0;
}

.icon a {
  color: #abc5e6;
  transition: all 0.5s;
}

.icon a:hover {
  color: #63f2d0;
}

@media screen and (max-width: 960px) {
  .works-container {
    display: flex;
    flex-direction: column;
  }

  .works-container h1 {
    margin: 0;
  }

  .works-wrapper {
    width: 100%;
    height: auto;
    position: relative;
    margin: 50px 0;
  }

  .works-wrapper:nth-child(odd) .img-wrapper {
    margin: 0;
  }

  .img-wrapper {
    height: 100%;
    width: 100%;
    position: relative;
    margin: 0;
  }

  .img-wrapper a {
    display: inline-block;
    width: 100%;
    height: 100%;
  }

  .img-wrapper::before {
    height: 100%;
    background-color: #0c1d32;
    box-shadow: 0 10px 30px -15px rgba(2, 12, 17, 0.7);
    opacity: 0.9;
  }

  .text-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 70%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    background-color: transparent;
  }

  .top-line {
    margin-top: 1rem;
    margin-left: 1rem;
  }

  .tec-list {
    margin-left: 1rem;
  }

  .top-line,
  .text-wrapper p,
  .text-wrapper ul,
  .text-wrapper ul li,
  .tec-list,
  .works-link,
  .works-link span,
  .works-link a {
    background-color: transparent;
  }

  .works-img {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 480px) {
  .text-wrapper {
    top: 50%;
  }

  .text-wrapper p {
    margin: 0;
  }
}
